import {area} from './assets/js/city.js'
import {host} from './const'

export let areaFilter = (code) => {
  if (code && code.length > 5) {
    return area.get(code);
  }
  return "";
};

export let hourPrice = (price) => {
  return price + "/";
};

export let imgHost = (path) => {
  return host.imgHost + path;
};

export let normMoney = (money) => {
  let mm = money * 1;
  let m = Math.round(mm).toString();
  if(m.length > 0 && m.length < 5){
    mm = parseFloat((mm + "").replace(/[^\d\.-]/g, "")).toFixed(2) + "";
    var l = mm.split(".")[0].split("").reverse(),
        r = mm.split(".")[1],
        t = "";
    for(var i = 0; i < l.length; i ++ ){
      t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? "," : "");
    }
    return t.split("").reverse().join("") + "." + r;
  }
  if(m.length > 4 && m.length < 7){
    return (mm/10000).toFixed(2) + "";
  }
  if(m.length == 7){
    return (mm/1000000).toFixed(2) + "";
  }
  if(m.length == 8){
    return (mm/10000000).toFixed(2) + "";
  }
  if(m.length > 8 && m.length < 11){
    return (mm/100000000).toFixed(2) + "";
  }
  if(m.length == 11){
    return (mm/10000000000).toFixed(2) + "";
  }
  if(m.length == 12){
    return (mm/100000000000).toFixed(2) + "";
  }
  if(m.length > 12){
    return (mm/1000000000000).toFixed(2) + "";
  }
};

export let numToChinese = (num)=> {
  let n = parseInt(num);
  switch (n) {
    case 1:
      return '';
    case 2:
      return '';
    case 3:
      return '';
    default:
      return num;
  }
};


export let zeroFill = (val, length = 2)=> {
  var len = val.toString().length;
  while (len < length) {
    val = "0" + val;
    len++;
  }
  return val;
};


