export let orderStatus = (status,orderType) => {
  switch (status) {
    case 0:
      return '';
    case 1:
      return orderType == 0 ?'':'';
    case 2:
      return '';
    case 9:
      return '';
    case 10:
      return '';
  }
};

export let ellipsis = (str, len = 20) => {
  if (null == str) {
    return '';
  };
  if (str.length < len) {
    return str;
  }
  return str.substring(0, len) + "...";
};


