export let phoneMask = (phone) => {
  phone = phone + '';
  let _phone = phone.substring(0,3) + '****' + phone.substring(7,11);
  return _phone;
};

/*datestr 1990-1-1*/
export let dateFmtCHS = (datestr) => {
  var dateArr = datestr.split('-');
  var  newdate = datestr;
  if (dateArr.length == 3){
    newdate = dateArr[0] + '' + zeroFill(dateArr[1]) + '' + zeroFill(dateArr[2]) + '';
  }
  return newdate;

};

/*ex:2016.10.17 -> 10.17*/
export let dateFmtYM = (dateStr) => {
  if (dateStr){
    return dateStr.substring(5,dateStr.length);
  }
};

export let weekFormat = (week) => {
  var formatStr = '';
  //console.log(week);
  week = week + "";
  if (week) {
    var weekArr = week.split(',');
    weekArr.sort();
    if (weekArr.length == 7) {
      return '';
    }
    var workday = 0, weekend = 0;
    for (var index in weekArr) {
      switch (parseInt(weekArr[index])) {
        case 1:
          formatStr += '';
          workday++;
          break;
        case 2:
          formatStr += '';
          workday++;
          break;
        case 3:
          formatStr += '';
          workday++;
          break;
        case 4:
          formatStr += '';
          workday++;
          break;
        case 5:
          formatStr += '';
          workday++;
          break;
        case 6:
          formatStr += '';
          weekend++
          break;
        case 7:
          formatStr += '';
          weekend++
          break;
        default:
          break;
      }
    }
    if (weekArr.length == 5 && workday == 5) {
      return '';
    }

    if (weekArr.length == 2 && weekend == 2) {
      return '';
    }
  }
  return formatStr;
};

