let areas = [province, city, district];

export let area = {

  get(code)
  {
    if (code.substring((code.length - 2), code.length) == "00") {
      for (let i in city) {
        if (city[i].code == code) {
          return city[i].name;
        }
      }
    }
    if (code.substring((code.length - 4), code.length) == "0000") {
      for (let i in province) {
        if (province[i].code == code) {
          return province[i].name;
        }
      }
    }
    for (let i in district) {
      if (district[i].code == code) {
        return district[i].name;
      }
    }
  }
  ,
  getProvince(code)
  {

  }
  ,
  getDistrictList(code)
  {
    let cityFix = code.substring(0, 4);

    let cit;
    let cityCode = cityFix + "00";
    for (let i in city) {
      if (city[i].code == cityCode) {
        cit = city[i];
      }
    }
    let list = [];
    list.push(cit);
    for (let i in district) {
      if (district[i].code.substring(0, 4) == cityFix) {
        list.push(district[i]);
      }
    }
    return list;
  }
};



